import React from 'react'
import '../styles/grid-tokens.css'

interface TokensProps {
  tokens: Record<string, string>
  hasRemValue?: boolean
}

export function TokensGrid({ tokens, hasRemValue = false }: TokensProps) {
  return (
    <table className="tokens-grid">
      <thead>
        <th>Name</th>
        <th>Value</th>
        {hasRemValue && <th>Pixel</th>}
      </thead>
      <tbody>
        {Object.entries(tokens).map(([key, value]) => {
          return (
            <tr key={key}>
              <td>{key}</td>
              <td>{value}</td>

              {hasRemValue && (
                <td>{Number(value.replace('rem', '')) * 16} px</td>
              )}
            </tr>
          )
        })}
      </tbody>
    </table>
  )
}
